import React from 'react'
import { useStateValue } from './StateProvider';
import '../CssFolder/checkout.css'
import CheckOutItem from './CheckOutItem';

function Checkout() {

    const [{basket }, dispatch] = useStateValue();


    return (
        <div className="c_MainDiv">
            <img className="c_ad_image" src="https://m.media-amazon.com/images/G/31/AMS/IN/970X250-_desktop_banner.jpg"/>
            {basket?.length === 0 ? (
                <div className="tp1">
                    <h2>Your Shopping basket is empty</h2>
                    <p>you have no items is your basket..</p>
                </div>
            ):(
                <div className="tp1">
                    <h2>Your Shopping basket</h2>

                    {basket.map(item => (
                        <CheckOutItem
                            id={item.id}
                            title={item.title}
                            price={item.price}
                            image={item.image}
                            ratting={item.ratting}/>
                    ))}
                </div>
            )
            }
        </div>
    )
}

export default Checkout
