import React from 'react'
import '../CssFolder/product.css'
import { useStateValue } from './StateProvider';

function Product({ id, title, price, image, ratting }) {

    const [{basket }, dispatch] = useStateValue();

    const addToBasket=()=>{
        dispatch({
            type: 'ADD_TO_BASKET',
            item:{
                id: id,
                title: title,
                price:price,
                image:image,
                ratting: ratting
            }
        })
    }

    return (
        <div className="ProductsMainDiv">
            <div className="productInfoDiv">
                <p className="pTitle">{title}</p>
                <div className="priceDiv">
                    <small>Rs. </small>
                    <strong>{price}</strong>
                </div>
                <div className="rattingDiv">
                    {
                        Array(5)
                            .fill()
                            .map((_) => (
                                <p>⭐</p>
                            ))
                    }

                </div>
            </div>

            <img className="pImage" src={image} />

            <button onClick={addToBasket} className="pButton">Add to Basket</button>
        </div>
    )

   
}

export default Product
