import React from 'react'
import { useStateValue } from './StateProvider';
import '../CssFolder/checkoutiteam.css'

function CheckOutItem({ id, title, price, image, ratting }) {

    const [{ basket }, dispatch] = useStateValue();

    const removeFromBasket = () =>{
        dispatch({
            type:"REMOVE_FROM_BASKET",
            id:id
        })
    }

    return (
        <div className="m_MainDiv">
            <img className="m_img" src={image} />

            <div className="m_info">

            <p className="m_title">{title}</p>
            <div className="m_priceDiv">
                <small>Rs. </small>
                <strong>{price}</strong>
            </div>

            <div className="rattingDiv">
                    {
                        Array(5)    
                            .fill()
                            .map((_) => (
                                <p>⭐</p>
                            ))
                    }

                </div>

                <button onClick={removeFromBasket} className="pButton">Remove from Basket</button>

                </div>
        </div>
    )
}

export default CheckOutItem
